import { useState, useRef, useEffect } from "react";
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import styles from '@/views/partials/card.module.css';
import clsx from 'clsx';


function DisplayCard(props: any) {
    const [card] = useState({
        task: props.task,
        description: props.description,
        due_date: props.due_date,
        priority: props.priority
    });
    return (
        <div className={styles.cardParent} style={{height: '300px'}}>
            <div className={styles.card}>
                <h3>{card.task}</h3>
                <p>{card.description}</p>
                <div className={styles.bottomSection}>
                    <p>Due: {card.due_date && new Date(card.due_date).toDateString() ||  'N/A'}</p>
                    <p>Priority: {card.priority}</p>
                </div>
                <div className={styles.icons}>
                    <button className={clsx(styles.edit, styles['my-button'])}>
                        <EditIcon style={{ color: 'white' }} />
                    </button>
                    <button className={clsx(styles.delete, styles['my-button'])}>
                        <DeleteIcon style={{ color: 'white' }} />
                    </button>
                </div>       
            </div>
        </div>
    );

}

export default DisplayCard;