import Card from '@/views/partials/card';//one way of setting path
import DisplayCard from '@/views/partials/displayCard';//another way of setting path
import {useState, useEffect, useRef} from 'react';
import axios from 'axios';
import styles from '@/views/partials/card.module.css'

export default function Content1(props: any) {
    interface Task {
        id: number;
        task: string;
        description: string;
        due_date: Date;
        columnname: string;
        priority: string;
    }
    const [tasks, setTasks] = useState<Task[]>([]);

    useEffect(() => {
        const fetchTasks = async() => {
            try {
                const response = await axios.get('/api/tasks');
                setTasks(response.data);
            } catch (error) {
                console.error('Error fetching tasks:', error);
            }
        };
        fetchTasks();
    }, []);

    //the task with the type: "any" is coming from the Card component when the form is submitted where it was changed from props.onAdd(card);
    const addTask = async (task: any) => {
        const columnname = 'To Do'; // Default column for new tasks
        const addTaskData = [ task.task, task.description, task.due_date, columnname, task.priority ];
        try {
            const response = await axios.post('/api/tasks', addTaskData);
            setTasks([...tasks, response.data]);
        } catch (error) {
            console.error('Error adding task:', error);
        }
    };

    const [isEditing, setIsEditing] = useState(false);
    const [editedTask, setEditedTask] = useState(tasks)

    function editForm(){
        setIsEditing(true);
    }

    
    return (
        <div className={props.className}>
            <div className={styles.content}>
                {tasks.map((task, index) => (
                    isEditing ?
                    <Card
                       key={index}
                       task={task.task}
                       description={task.description} 
                       due_date={task.due_date} 
                       priority={task.priority} 
                    />
                    :
                    <DisplayCard 
                       key={index}
                       task={task.task}
                       description={task.description} 
                       due_date={task.due_date} 
                       priority={task.priority}
                       onEdit={editForm}
                    />
                ))}
                <Card onAdd={addTask} />
            </div>
        </div>
    );
} 